.button2 {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 32px;
  background: #fff;
  border: 1px solid rgba(153, 153, 153, 1);
  border-radius: 4px;
  padding: 4px 1em;
  margin: 10px;
  position: relative;
  overflow: hidden;
}

.button2:focus {
  outline: none;
  border-color: #000;
}

.button2>.circle {
  border-radius: 50%;
  height: 10px;
  width: 10px;
  background-color: yellow;
  opacity: 0.5;
  display: inline-block;
  -webkit-animation: 1s big forwards;
          animation: 1s big forwards;
  position: absolute;
  z-index: 0;
}

.button2 > .value{
  position: relative;
  z-index: 1;
}

@-webkit-keyframes big {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100% {
    -webkit-transform: scale(15);
            transform: scale(15)
  }
}

@keyframes big {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100% {
    -webkit-transform: scale(15);
            transform: scale(15)
  }
}


